export const reverseMapping = (obj) => {
  const reversed = {};
  Object.keys(obj).forEach((key) => {
      reversed[obj[key]] = key
  })
  return reversed
}

export const slugify = text =>
  text
    .toString()
    .normalize('NFD')
    .replace(/[\u0300-\u036f]/g, '')
    .toLowerCase()
    .trim()
    .replace(/\s+/g, '-')
    .replace(/[^\w-]+/g, '')
    .replace(/--+/g, '-')
